.btnVoltar {
  position: absolute;
  left: 0;
  top: 0;
  width: 2rem;
  height: 2rem;
  display: grid;
  place-items: center;
  z-index: 9;
}

.btnVoltar svg {
  transform: scale(.5);
  fill: #ffffff23;
  transition: .1s;
}

.btnVoltar:hover svg {
  fill: rgba(255, 255, 255, 0.8);
}

.title {
  opacity: .6;
  margin-bottom: 3rem;
}